	  	  width: 90px;
	  	  font-size: 32px;
	  	  line-height: 90px;
	  	  text-align: center;
	  	  height: 90px;
	  	}
	
	.add-update-preview /deep/ .el-upload .el-icon-plus {
	  	  border: 1px solid #000;
	  	  cursor: pointer;
	  	  border-radius: 6px;
	  	  color: #000;
	  	  width: 90px;
